html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
  position: relative;
}

html {
  font-size: 14px;
}

body {
  margin: 0;
  font-size: 14px;
  background-color: #fff;
  font-family: Arial, sans-serif;
  line-height: 1.1;
}

.wrapper {
  min-height: 100%;
  height: auto;
  overflow: hidden;
  position: relative;
}

.container {
  display: flex;
  align-items: center;
  width: 960px;
  height: 100vh;
  margin: 0 auto;
  position: relative;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
}

* {
  box-sizing: border-box;
}

.calc {
  width: 960px;
  height: 563px;
  padding-top: 57px;
  background-color: #fff;
  border: 2px solid #eee;
}

.calc__top {
  width: 100%;
  height: 158px;
}

.calc__down {
  position: relative;
  width: 100%;
  height: 260px;
}

.calc__stroke {
  position: absolute;
  left: 77px;
  bottom: 155px;
  z-index: 2;
}

.calc__stroke-block {
  display: inline-block;
}

.calc__stroke-img {
  display: inline-block;
}

.calc__stroke-img--first {
  background-size: contain;
}

.calc__stroke-img--second {
  background-size: contain;
}

.calc__scale {
  position: absolute;
  bottom: 90px;
  left: 43px;
}

.sum {
  display: flex;
  justify-content: center;
}

.sum__number {
  width: 34px;
  height: 45px;
  font-size: 40px;
  color: #000;
  text-align: center;
}

.sum__number--total,
.sum__number--question {
  width: 67px;
}

.sum__input {
  background-color: #fff;
  border: none;
  border-radius: 5px;
}

.sum__input:focus {
  outline: none !important;
}

input::-moz-placeholder {
  color: #000;
}

input::-ms-input-placeholder {
  color: #000;
}

input::-webkit-input-placeholder {
  color: #000;
}

.error-value {
  color: #f61747;
}

.error-background {
  border-radius: 5px;
  background-color: #f7a94e;
}

.active {
  border: 3px solid #afafaf;
}

.hidden {
  display: none;
}